@media only screen and (max-width:855px)
{
    .main__cards{
        grid-template-columns: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 0;
    }

    .charts{
        grid-template-columns: 1fr;
        margin-top:30px;
    }
}
.navbar{
    background: #ffffff;
    grid-area:nav;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding:0 30px 0 30px;
    border-bottom: 1px solid lightgray;
}

.nav_icon{
    display: none;
}

.nav_icon > i {
    font-size: 26px;
    color: #a5aaad;
}

.navbar__left >a {
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight:700;     
}

.navbar__left .active_link{
    color: #265acc;
    border-bottom: 3px solid #265acc;
    padding-bottom: 12px;
}

.navbar__right{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.navbar__right >a
{
    margin-left: 20px;
    text-decoration: none;
}

.navbar__right > a > i{
    color:#a5aaad;
    font-size: 16px;
    border-radius: 50px;
    background: #ffffff;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #ffffff;
    padding:7px;
}

@media only screen and (max-width:978px)
{
    .nav_icon{
        display:inline;
    }
}

@media only screen and (max-width:480px)
{
    .navbar__left{
        display: none;
    }
}
#sidebar{
    background: #020509;
    grid-area:sidebar;
    overflow-y:auto;
    padding: 20px;
    transition: all 0.5s;
}

 .sidebar__title{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #f3f4f6;
    margin-bottom: 30px;
}

.sidebar__img{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.sidebar__title > div > img{
    width: 75px;
    object-fit: contain;
}

.sidebar__title > div> h1{
    font-size: 18px;
    display: inline;
  
}

.sidebar__title>i{
    font-size: 18px;
    display: none;
}

.sidebar__link{
    color:#f3f4f6;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}

.active_menu_link{
    background: rgba(62, 161, 117, 0.3);
    color:#3ea175;
}

.active_menu_link a{
    color:#3ea175 !important;
}

.sidebar__link >a{
    text-decoration: none;
    color:#a5aaad;
    font-weight: 700;
}

.sidebar__link>i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-responsive{
    display:inline !important;
    z-index:9999 !important;
    left:0 !important;
    position: absolute;
}

@media only screen and (max-width:978px){
    #sidebar{
        display: none;
    }

    .sidebar__title>i{
        display: inline;
    }
} 
