#sidebar{
    background: #020509;
    grid-area:sidebar;
    overflow-y:auto;
    padding: 20px;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
}

 .sidebar__title{
    display:flex;
    justify-content: space-between;
    align-items: center;
    color: #f3f4f6;
    margin-bottom: 30px;
}

.sidebar__img{
    display: flex;
    align-items: center;
}

.sidebar__title > div > img{
    width: 75px;
    object-fit: contain;
}

.sidebar__title > div> h1{
    font-size: 18px;
    display: inline;
  
}

.sidebar__title>i{
    font-size: 18px;
    display: none;
}

.sidebar__link{
    color:#f3f4f6;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}

.active_menu_link{
    background: rgba(62, 161, 117, 0.3);
    color:#3ea175;
}

.active_menu_link a{
    color:#3ea175 !important;
}

.sidebar__link >a{
    text-decoration: none;
    color:#a5aaad;
    font-weight: 700;
}

.sidebar__link>i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-responsive{
    display:inline !important;
    z-index:9999 !important;
    left:0 !important;
    position: absolute;
}

@media only screen and (max-width:978px){
    #sidebar{
        display: none;
    }

    .sidebar__title>i{
        display: inline;
    }
} 